import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { sanityClient } from "../sanity/client";
import imageUrlBuilder from "@sanity/image-url";

const builder = imageUrlBuilder(sanityClient);

function urlFor(source: any) {
  return builder.image(source);
}

interface Product {
  _id: string;
  name: string;
  slug: { current: string };
  price: number;
  images?: Array<{ asset: any; alt?: string }>;
}

interface Category {
  title: string;
  image?: { asset: any };
  link: string;
}

interface HomePage {
  heroTitle: string;
  heroSubtitle?: string;
  heroImage?: { asset: any };
  featuredProducts?: Product[];
  secondaryHeroTitle?: string;
  secondaryHeroSubtitle?: string;
  secondaryHeroImage?: { asset: any };
  secondaryHeroButtonText?: string;
  secondaryHeroButtonLink?: string;
  categories?: Category[];
  newsletterTitle?: string;
  newsletterText?: string;
  newsletterButtonText?: string;
}

const Home: React.FC = () => {
  const [homeData, setHomeData] = useState<HomePage | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchHomeData = async () => {
      try {
        const query = `*[_type == "homePage" && _id == "homePage"][0] {
          heroTitle,
          heroSubtitle,
          heroImage,
          featuredProducts[]-> {
            _id,
            name,
            slug,
            price,
            images[] {
              asset,
              alt
            }
          },
          secondaryHeroTitle,
          secondaryHeroSubtitle,
          secondaryHeroImage,
          secondaryHeroButtonText,
          secondaryHeroButtonLink,
          categories[] {
            title,
            image,
            link
          },
          newsletterTitle,
          newsletterText,
          newsletterButtonText
        }`;
        const data = await (sanityClient.fetch as any)(query);
        console.log("Home page data:", data);
        setHomeData(data);
      } catch (error) {
        console.error("Error fetching home page:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchHomeData();
  }, []);

  if (loading) {
    return (
      <div
        className="container"
        style={{ textAlign: "center", padding: "var(--spacing-xl)" }}
      >
        Loading...
      </div>
    );
  }

  if (!homeData) {
    return (
      <div className="container">
        <div className="card" style={{ textAlign: "center" }}>
          <h2>Home page not configured yet.</h2>
          <p>Please add content in Sanity Studio.</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      {/* Hero Section */}
      <section className="section bg-gray-light">
        <div className="container">
          <h1 className="hero-title">{homeData.heroTitle}</h1>
          {homeData.heroSubtitle && (
            <p
              style={{
                fontSize: "var(--font-size-xl, 1.25rem)",
                color: "var(--color-gray)",
                marginTop: "var(--spacing-md)",
              }}
            >
              {homeData.heroSubtitle}
            </p>
          )}
          {homeData.heroImage && (
            <img
              src={urlFor(homeData.heroImage).url()}
              alt="Hero"
              style={{
                maxWidth: "100%",
                height: "auto",
                borderRadius: "var(--card-border-radius)",
                marginTop: "var(--spacing-xl)",
                boxShadow: "var(--card-shadow)",
              }}
            />
          )}
        </div>
      </section>

      {/* Featured Products Grid */}
      {homeData.featuredProducts && homeData.featuredProducts.length > 0 && (
        <section className="section">
          <div className="container">
            <h2>Featured Products</h2>
            <div
              className="grid grid-3"
              style={{ marginTop: "var(--spacing-lg)" }}
            >
              {homeData.featuredProducts.map((product) => (
                <div key={product._id} className="card">
                  <Link
                    to={`/products/${product.slug.current}`}
                    style={{ textDecoration: "none", color: "inherit" }}
                  >
                    {product.images?.[0] && (
                      <img
                        src={urlFor(product.images[0]).width(300).url()}
                        alt={product.images[0].alt || product.name}
                        style={{
                          width: "100%",
                          height: "200px",
                          objectFit: "cover",
                          borderRadius: "var(--card-border-radius)",
                          marginBottom: "var(--spacing-md)",
                        }}
                      />
                    )}
                    <h3>{product.name}</h3>
                    <p
                      className="text-primary"
                      style={{
                        fontSize: "var(--font-size-lg, 1.125rem)",
                        fontWeight: 600,
                      }}
                    >
                      ${product.price}
                    </p>
                  </Link>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* Secondary Hero Section */}
      {homeData.secondaryHeroTitle && (
        <section className="section">
          <div className="container">
            <div
              className="grid grid-2"
              style={{ alignItems: "center", gap: "var(--spacing-2xl)" }}
            >
              <div>
                <h2>{homeData.secondaryHeroTitle}</h2>
                {homeData.secondaryHeroSubtitle && (
                  <p
                    style={{
                      color: "var(--color-gray)",
                      marginTop: "var(--spacing-md)",
                      marginBottom: "var(--spacing-lg)",
                    }}
                  >
                    {homeData.secondaryHeroSubtitle}
                  </p>
                )}
                {homeData.secondaryHeroButtonText && (
                  <Link
                    to={homeData.secondaryHeroButtonLink || "/products"}
                    className="button"
                    style={{ textDecoration: "none" }}
                  >
                    {homeData.secondaryHeroButtonText}
                  </Link>
                )}
              </div>
              {homeData.secondaryHeroImage && (
                <img
                  src={urlFor(homeData.secondaryHeroImage).url()}
                  alt="Secondary Hero"
                  style={{
                    width: "100%",
                    height: "auto",
                    borderRadius: "var(--card-border-radius)",
                  }}
                />
              )}
            </div>
          </div>
        </section>
      )}

      {/* Categories Section */}
      <section className="section bg-gray-light">
        <div className="container">
          <h2>Shop by Category</h2>
          <div
            className="grid grid-4"
            style={{ marginTop: "var(--spacing-lg)" }}
          >
            {homeData.categories?.map((category, index) => (
              <div key={index} className="card">
                <Link
                  to={category.link || "/products"}
                  style={{ textDecoration: "none", color: "inherit" }}
                >
                  <h3>{category.title}</h3>
                  {category.image && (
                    <img
                      src={urlFor(category.image).width(300).url()}
                      alt={category.title}
                      style={{
                        width: "100%",
                        height: "150px",
                        objectFit: "cover",
                        borderRadius: "var(--card-border-radius)",
                        marginTop: "var(--spacing-sm)",
                      }}
                    />
                  )}
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Newsletter Section */}
      {homeData.newsletterTitle && (
        <section className="section">
          <div className="container">
            <div
              className="card"
              style={{
                textAlign: "center",
                maxWidth: "600px",
                margin: "0 auto",
              }}
            >
              <h2>{homeData.newsletterTitle}</h2>
              {homeData.newsletterText && (
                <p
                  style={{
                    color: "var(--color-gray)",
                    marginTop: "var(--spacing-md)",
                    marginBottom: "var(--spacing-lg)",
                  }}
                >
                  {homeData.newsletterText}
                </p>
              )}
              {homeData.newsletterButtonText && (
                <button className="button">
                  {homeData.newsletterButtonText}
                </button>
              )}
            </div>
          </div>
        </section>
      )}
    </div>
  );
};

export default Home;
