import { sanityClient } from "../sanity/client";

interface FontFile {
  _type: "file";
  asset: {
    _ref: string;
    _type: "reference";
  };
  fontWeight: string;
  fontStyle: string;
}

interface Settings {
  primaryFontFiles?: FontFile[];
  headingFontFiles?: FontFile[];
  primaryFontName?: string;
  headingFontName?: string;
}

export async function generateFontCSS(): Promise<string> {
  try {
    const query = `
      *[_type == "settings"][0]{
        primaryFontFiles[]{
          ...,
          "url": asset->url
        },
        headingFontFiles[]{
          ...,
          "url": asset->url
        },
        primaryFontName,
        headingFontName
      }
    `;
    const settings: Settings = await (sanityClient.fetch as any)(query);

    let css = "";

    // Generate @font-face rules for primary font
    if (settings.primaryFontFiles && settings.primaryFontName) {
      const primaryFontFamily = settings.primaryFontName
        .split(",")[0]
        .replace(/['"]/g, "");

      settings.primaryFontFiles.forEach((font: any) => {
        if (font.url) {
          css += `
@font-face {
  font-family: "${primaryFontFamily}";
  src: url("${font.url}") format("${getFormatFromUrl(font.url)}");
  font-weight: ${font.fontWeight || "400"};
  font-style: ${font.fontStyle || "normal"};
  font-display: swap;
}`;
        }
      });
    }

    // Generate @font-face rules for heading font
    if (settings.headingFontFiles && settings.headingFontName) {
      const headingFontFamily = settings.headingFontName
        .split(",")[0]
        .replace(/['"]/g, "");

      settings.headingFontFiles.forEach((font: any) => {
        if (font.url) {
          css += `
@font-face {
  font-family: "${headingFontFamily}";
  src: url("${font.url}") format("${getFormatFromUrl(font.url)}");
  font-weight: ${font.fontWeight || "400"};
  font-style: ${font.fontStyle || "normal"};
  font-display: swap;
}`;
        }
      });
    }

    return css;
  } catch (error) {
    console.error("Error generating font CSS:", error);
    return "";
  }
}

function getFormatFromUrl(url: string): string {
  const extension = url.split(".").pop()?.toLowerCase();
  switch (extension) {
    case "woff2":
      return "woff2";
    case "woff":
      return "woff";
    case "ttf":
      return "truetype";
    case "otf":
      return "opentype";
    default:
      return "truetype";
  }
}

export async function loadCustomFonts(): Promise<void> {
  const fontCSS = await generateFontCSS();

  if (fontCSS) {
    // Remove existing custom font styles
    const existingStyle = document.getElementById("custom-fonts");
    if (existingStyle) {
      existingStyle.remove();
    }

    // Add new font styles
    const style = document.createElement("style");
    style.id = "custom-fonts";
    style.textContent = fontCSS;
    document.head.appendChild(style);
  }
}
