import React, { useEffect, useState } from "react";
import { sanityClient } from "../sanity/client";

interface AboutPage {
  heroTitle: string;
  heroText?: string;
  heroImage?: { asset: { url: string }; alt?: string };
  fabricTitle?: string;
  fabricText?: string;
  fabricImage?: { asset: { url: string }; alt?: string };
}

const About: React.FC = () => {
  const [about, setAbout] = useState<AboutPage | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAbout = async () => {
      try {
        const query = `*[_type == "about" && _id == "aboutPage"][0] {
          heroTitle,
          heroText,
          heroImage {
            asset -> { url },
            alt
          },
          fabricTitle,
          fabricText,
          fabricImage {
            asset -> { url },
            alt
          }
        }`;
        const data = await (sanityClient.fetch as any)(query);
        setAbout(data);
      } catch (error) {
        console.error("Error fetching about page:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchAbout();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (!about) {
    return <div>About page not found.</div>;
  }

  return (
    <div>
      <h1>About</h1>

      {/* Hero Section */}
      <section>
        <h2>{about.heroTitle}</h2>
        {about.heroText && <p>{about.heroText}</p>}
        {about.heroImage && (
          <img
            src={about.heroImage.asset.url}
            alt={about.heroImage.alt || "Hero image"}
            width="600"
          />
        )}
      </section>

      <hr />

      {/* Fabric Section */}
      {about.fabricTitle && (
        <section>
          <h2>{about.fabricTitle}</h2>
          {about.fabricText && <p>{about.fabricText}</p>}
          {about.fabricImage && (
            <img
              src={about.fabricImage.asset.url}
              alt={about.fabricImage.alt || "Fabric image"}
              width="600"
            />
          )}
        </section>
      )}
    </div>
  );
};

export default About;
