interface SettingsData {
  // Brand Colors
  primaryColor?: { hex: string };
  blackColor?: { hex: string };
  whiteColor?: { hex: string };
  grayLightColor?: { hex: string };
  grayColor?: { hex: string };
  grayDarkColor?: { hex: string };

  // Typography
  primaryFontName?: string;
  headingFontName?: string;
  heroFontSize?: number;
  h1FontSize?: number;
  h2FontSize?: number;
  h3FontSize?: number;
  baseFontSize?: number;
  smallFontSize?: number;

  // Spacing
  baseSpacing?: number;
  containerMaxWidth?: number;
  sectionPadding?: number;

  // Layout
  headerHeight?: number;
  footerHeight?: number;

  // Buttons
  buttonBorderRadius?: number;
  buttonPadding?: number;

  // Cards
  cardBorderRadius?: number;
  cardShadow?: string;
}

export function applySettingsToCSS(settings: SettingsData) {
  const root = document.documentElement;

  // Brand Colors
  if (settings.primaryColor?.hex) {
    root.style.setProperty("--color-primary", settings.primaryColor.hex);
  }
  if (settings.blackColor?.hex) {
    root.style.setProperty("--color-black", settings.blackColor.hex);
  }
  if (settings.whiteColor?.hex) {
    root.style.setProperty("--color-white", settings.whiteColor.hex);
  }
  if (settings.grayLightColor?.hex) {
    root.style.setProperty("--color-gray-light", settings.grayLightColor.hex);
  }
  if (settings.grayColor?.hex) {
    root.style.setProperty("--color-gray", settings.grayColor.hex);
  }
  if (settings.grayDarkColor?.hex) {
    root.style.setProperty("--color-gray-dark", settings.grayDarkColor.hex);
  }

  // Typography
  if (settings.primaryFontName) {
    root.style.setProperty("--font-primary", settings.primaryFontName);
  }
  if (settings.headingFontName) {
    root.style.setProperty("--font-heading", settings.headingFontName);
  }
  if (settings.heroFontSize) {
    root.style.setProperty("--font-size-hero", `${settings.heroFontSize}rem`);
  }
  if (settings.h1FontSize) {
    root.style.setProperty("--font-size-h1", `${settings.h1FontSize}rem`);
  }
  if (settings.h2FontSize) {
    root.style.setProperty("--font-size-h2", `${settings.h2FontSize}rem`);
  }
  if (settings.h3FontSize) {
    root.style.setProperty("--font-size-h3", `${settings.h3FontSize}rem`);
  }
  if (settings.baseFontSize) {
    root.style.setProperty("--font-size-base", `${settings.baseFontSize}rem`);
  }
  if (settings.smallFontSize) {
    root.style.setProperty("--font-size-small", `${settings.smallFontSize}rem`);
  }

  // Spacing
  if (settings.baseSpacing) {
    root.style.setProperty("--spacing-base", `${settings.baseSpacing}px`);
    root.style.setProperty("--spacing-xs", `${settings.baseSpacing * 0.25}px`);
    root.style.setProperty("--spacing-sm", `${settings.baseSpacing * 0.5}px`);
    root.style.setProperty("--spacing-md", `${settings.baseSpacing}px`);
    root.style.setProperty("--spacing-lg", `${settings.baseSpacing * 1.5}px`);
    root.style.setProperty("--spacing-xl", `${settings.baseSpacing * 2}px`);
    root.style.setProperty("--spacing-2xl", `${settings.baseSpacing * 3}px`);
    root.style.setProperty("--spacing-3xl", `${settings.baseSpacing * 4}px`);
  }

  if (settings.containerMaxWidth) {
    root.style.setProperty(
      "--container-max-width",
      `${settings.containerMaxWidth}px`
    );
  }
  if (settings.sectionPadding) {
    root.style.setProperty("--section-padding", `${settings.sectionPadding}px`);
  }

  // Layout
  if (settings.headerHeight) {
    root.style.setProperty("--header-height", `${settings.headerHeight}px`);
  }
  if (settings.footerHeight) {
    root.style.setProperty("--footer-height", `${settings.footerHeight}px`);
  }

  // Buttons
  if (settings.buttonBorderRadius) {
    root.style.setProperty(
      "--button-border-radius",
      `${settings.buttonBorderRadius}px`
    );
  }
  if (settings.buttonPadding) {
    root.style.setProperty("--button-padding", `${settings.buttonPadding}px`);
  }

  // Cards
  if (settings.cardBorderRadius) {
    root.style.setProperty(
      "--card-border-radius",
      `${settings.cardBorderRadius}px`
    );
  }
  if (settings.cardShadow) {
    root.style.setProperty("--card-shadow", settings.cardShadow);
  }
}

// Generate complete CSS from settings for server-side rendering or static generation
export function generateSettingsCSS(settings: SettingsData): string {
  const cssVars: string[] = [];

  // Brand Colors
  if (settings.primaryColor?.hex) {
    cssVars.push(`  --color-primary: ${settings.primaryColor.hex};`);
  }
  if (settings.blackColor?.hex) {
    cssVars.push(`  --color-black: ${settings.blackColor.hex};`);
  }
  if (settings.whiteColor?.hex) {
    cssVars.push(`  --color-white: ${settings.whiteColor.hex};`);
  }
  if (settings.grayLightColor?.hex) {
    cssVars.push(`  --color-gray-light: ${settings.grayLightColor.hex};`);
  }
  if (settings.grayColor?.hex) {
    cssVars.push(`  --color-gray: ${settings.grayColor.hex};`);
  }
  if (settings.grayDarkColor?.hex) {
    cssVars.push(`  --color-gray-dark: ${settings.grayDarkColor.hex};`);
  }

  // Typography
  if (settings.primaryFontName) {
    cssVars.push(`  --font-primary: ${settings.primaryFontName};`);
  }
  if (settings.headingFontName) {
    cssVars.push(`  --font-heading: ${settings.headingFontName};`);
  }
  if (settings.heroFontSize) {
    cssVars.push(`  --font-size-hero: ${settings.heroFontSize}rem;`);
  }
  if (settings.h1FontSize) {
    cssVars.push(`  --font-size-h1: ${settings.h1FontSize}rem;`);
  }
  if (settings.h2FontSize) {
    cssVars.push(`  --font-size-h2: ${settings.h2FontSize}rem;`);
  }
  if (settings.h3FontSize) {
    cssVars.push(`  --font-size-h3: ${settings.h3FontSize}rem;`);
  }
  if (settings.baseFontSize) {
    cssVars.push(`  --font-size-base: ${settings.baseFontSize}rem;`);
  }
  if (settings.smallFontSize) {
    cssVars.push(`  --font-size-small: ${settings.smallFontSize}rem;`);
  }

  // Spacing
  if (settings.baseSpacing) {
    cssVars.push(`  --spacing-base: ${settings.baseSpacing}px;`);
    cssVars.push(`  --spacing-xs: ${settings.baseSpacing * 0.25}px;`);
    cssVars.push(`  --spacing-sm: ${settings.baseSpacing * 0.5}px;`);
    cssVars.push(`  --spacing-md: ${settings.baseSpacing}px;`);
    cssVars.push(`  --spacing-lg: ${settings.baseSpacing * 1.5}px;`);
    cssVars.push(`  --spacing-xl: ${settings.baseSpacing * 2}px;`);
    cssVars.push(`  --spacing-2xl: ${settings.baseSpacing * 3}px;`);
    cssVars.push(`  --spacing-3xl: ${settings.baseSpacing * 4}px;`);
  }

  if (settings.containerMaxWidth) {
    cssVars.push(`  --container-max-width: ${settings.containerMaxWidth}px;`);
  }
  if (settings.sectionPadding) {
    cssVars.push(`  --section-padding: ${settings.sectionPadding}px;`);
  }

  // Layout
  if (settings.headerHeight) {
    cssVars.push(`  --header-height: ${settings.headerHeight}px;`);
  }
  if (settings.footerHeight) {
    cssVars.push(`  --footer-height: ${settings.footerHeight}px;`);
  }

  // Buttons
  if (settings.buttonBorderRadius) {
    cssVars.push(`  --button-border-radius: ${settings.buttonBorderRadius}px;`);
  }
  if (settings.buttonPadding) {
    cssVars.push(`  --button-padding: ${settings.buttonPadding}px;`);
  }

  // Cards
  if (settings.cardBorderRadius) {
    cssVars.push(`  --card-border-radius: ${settings.cardBorderRadius}px;`);
  }
  if (settings.cardShadow) {
    cssVars.push(`  --card-shadow: ${settings.cardShadow};`);
  }

  return `:root {\n${cssVars.join("\n")}\n}`;
}
