import { useEffect, useState } from "react";
import { sanityClient } from "../sanity/client";

interface SettingsData {
  // Brand Colors
  primaryColor?: { hex: string };
  blackColor?: { hex: string };
  whiteColor?: { hex: string };
  grayLightColor?: { hex: string };
  grayColor?: { hex: string };
  grayDarkColor?: { hex: string };

  // Typography
  primaryFontName?: string;
  headingFontName?: string;
  heroFontSize?: number;
  h1FontSize?: number;
  h2FontSize?: number;
  h3FontSize?: number;
  baseFontSize?: number;
  smallFontSize?: number;

  // Spacing
  baseSpacing?: number;
  containerMaxWidth?: number;
  sectionPadding?: number;

  // Layout
  headerHeight?: number;
  footerHeight?: number;

  // Buttons
  buttonBorderRadius?: number;
  buttonPadding?: number;

  // Cards
  cardBorderRadius?: number;
  cardShadow?: string;
}

export function useSettings() {
  const [settings, setSettings] = useState<SettingsData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchSettings = async () => {
      try {
        const query = `
          *[_type == "settings"][0]{
            primaryColor,
            blackColor,
            whiteColor,
            grayLightColor,
            grayColor,
            grayDarkColor,
            primaryFontName,
            headingFontName,
            heroFontSize,
            h1FontSize,
            h2FontSize,
            h3FontSize,
            baseFontSize,
            smallFontSize,
            baseSpacing,
            containerMaxWidth,
            sectionPadding,
            headerHeight,
            footerHeight,
            buttonBorderRadius,
            buttonPadding,
            cardBorderRadius,
            cardShadow
          }
        `;
        const data = await (sanityClient.fetch as any)(query);

        setSettings(data);
        setLoading(false);
      } catch (err) {
        setError(
          err instanceof Error ? err.message : "Failed to fetch settings"
        );
        setLoading(false);
        console.error("Error fetching settings:", err);
      }
    };

    fetchSettings();
  }, []);

  return { settings, loading, error };
}
