import { useEffect, useState } from "react";
import { useSettings } from "./useSettings";
import { useFontLoader } from "./useFontLoader";
import { applySettingsToCSS } from "../utils/settingsApplier";

export function useTheme() {
  const {
    settings,
    loading: settingsLoading,
    error: settingsError,
  } = useSettings();
  const { fontsLoaded, error: fontError } = useFontLoader();
  const [themeApplied, setThemeApplied] = useState(false);

  useEffect(() => {
    if (settings && !settingsLoading) {
      applySettingsToCSS(settings);
      setThemeApplied(true);
    }
  }, [settings, settingsLoading]);

  return {
    settings,
    fontsLoaded,
    themeApplied,
    loading: settingsLoading,
    error: settingsError || fontError,
    isReady: !settingsLoading && fontsLoaded && themeApplied,
  };
}
